<template>
  <div class="main">
    <div class="breadCrumb">
      <el-breadcrumb separator="/" :to="{path:'/manageDocument'}">
        <el-breadcrumb-item>项目管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-button @click="addTeam" class="buttonAddTeam" type="primary" icon="el-icon-s-custom">添加团队</el-button>
<!--    <el-input v-model="input" placeholder="请输入内容"></el-input>-->
<!--    <el-button style="display: inline-block;width: 200px" @click="addTeam" class="buttonAddTeam" type="primary" icon="el-icon-s-custom">添加好友</el-button>-->
    <add-team-modal v-if="addingTeam" v-on:closeModal="closeModal"></add-team-modal>
    <el-card class="box-card" v-if="this.teamName!==''">
      <div slot="header" class="clearfix">
        <span>{{this.teamName}}</span>
      </div>
    </el-card>
  </div>
</template>

<script>
import AddTeamModal from "@/components/Team/AddTeamModal";
import {getProjectManageInfo} from "@/api/projectManage";
import store from '@/store'

export default {
  name: "ManageProject",
  components: {
    AddTeamModal,
  },
  data() {
    return {
      addingTeam: false,
      teamName:'',
    }
  },
  async mounted() {
    let res = await getProjectManageInfo({username: store.state.user.username});
    console.log(res);
  },
  updated() {
    this.teamName = localStorage.getItem('teamName');
    console.log(this.teamName);
  },
  methods: {
    addTeam() {
      this.addingTeam = true;
    },
    closeModal() {
      this.addingTeam = false;
    }
  },
}
</script>

<style scoped lang="less">
.main {
  height: 100%;
}

::v-deep .breadCrumb {
  margin: 15px 0 0 15px;
  font-weight: 800 !important;
}

::v-deep .el-breadcrumb__inner {
  font-weight: 800 !important;
  cursor: pointer !important;
}

.buttonAddTeam {
  margin: 10px 0 0 15px;
}

::v-deep .el-input__inner {
  width: 20vw;
}
::v-deep .el-card {
  width: 350px;
  height: 250px;
  margin: 10px 0 0 15px;
}
</style>
